/* sass-lint:disable no-ids id-name-format */
@import 'Styles/variables.scss';
@import 'Styles/mixins.scss';

.home {
  display: flex;
  width: 100%;

  color: $color-white;

  align-items: center;
  flex-flow: column wrap;
  justify-content: center;

  margin-top: 15vh;

  @media screen and (max-width: 415px){
    margin-top: 0;
  }

  &-title {
    @include page-title;
    font-size: 24px;
    line-height: 1.3;
    padding: 0 10px;
    padding-bottom: 0;
    margin-bottom: 109px;

    overflow: hidden;
    text-overflow: ellipsis;

    @media (max-width: 992px) {
      width: 100%;
    }

    @media only screen and (max-height: 600px) {
      margin-top: 0;
    }
  }

  &-projects {
    display: flex;

    width: 100%;

    align-items: space-between;
    flex-flow: row nowrap;
    justify-content: center;

    @media (max-width: 720px) {
      flex-wrap: wrap;
    }

    &-item {
      position: relative;

      width: 130px;
      margin: 0px 20px;

      display: flex;
      flex-direction: column;

      color: rgba(62, 64, 73, .3);

      text-decoration: none;


      align-items: center;

      svg {
        width: 88px;
        height: 88px;
      }

      @media (max-width: 480px) {
        font-size: 14px;
      }

      @media (max-width: 720px) {
        margin-bottom: 30px;
      }


      &__title {
        position: absolute;
        bottom: -23px;

        width: 100px;

        justify-content: center;

        @media (max-width: 420px) {
          width: 84px;
        }
      }

      &__button {
        display: flex;
        flex-direction: column;

        margin: 0;
        padding: 0;

        cursor: pointer;

        color: $color-white;
        border: none;
        background: none;

        align-items: center;
        justify-content: center;

        &:active,
        &:focus {
          outline: 0;
          border: none;
        }
      }

      &__icon {
        width: auto;
        min-width: 67px;
        height: auto;

        transition: transform .3s;

        &:hover {
          transform: scale(1.1);
        }
      }

      &__name {
        font-size: 16px;
        margin-top: 10px;

        color: $default-text;
      }
    }
  }

  @media screen and (max-height: 750px) {
    &-title {
      margin-bottom: 50px;
    }
    &-projects-item {
      margin: 0;
      margin-bottom: 30px;
    }

    &-projects-item,
    &-projects-item__icon {
      svg,
      img {
        width: 60px;
        height: 60px;
      }
    }
  }
}

.home-projects-item__icon {
  &--second-disabled {
    path {
      fill: $color-white;
      opacity: 0.27;
    }
  }
}

.home-projects-item--coming-soon {
  position: relative;

  cursor: not-allowed;

  .home-projects-item__name {
    font-size: 14px;
    opacity: .3;
  }

  @media screen and (max-width: 480px) {
    &::before {
      bottom: -20px;
    }
  }
}
