.desktopForm {
  display: flex;
  flex-direction: column;
}

.fieldsWrapper {
  display: flex;
  justify-content: center;
}

.formPart {
  max-width: 400px;
  width: 100%;
  padding: 0 15px;
  margin: 0 10px;

  & > :first-child {
    margin-bottom: 52px;
  }

  & > :last-child {
    margin-top: 42px;
  }
}


.formFooter {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 40px;

  .button {
    width: 175px;
  }
}
