@import 'Styles/variables.scss';

.bar {
  position: relative;
  top: -40px;
  left: 0;

  display: flex;
  flex-direction: column;

  width: 57px;
  height: 200px;

  transform: translateX(-50%);
  text-align: center;

  color: $blue;
  background-color: rgba(255, 255, 255, 0);
  box-shadow: 0 2px 19px 0 rgba(0, 147, 253, .1);

  font-size: 10px;
  line-height: 1.2;

  justify-content: space-between;
}

.barCounter {
  padding: 10px;
}

.barDate {
  position: relative;
  z-index: 100;

  padding: 0 10px 10px 10px;

  pointer-events: none;
  line-height: 1.5;

  background-color: #fff;
}
