@import 'Styles/variables.scss';

.wrapper {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;

  max-width: 370px;
  width: 100%;

  color: $default-text;
}

.titleWrapper {
  display: flex;
  justify-content: space-between;
}

.blockedWrapper {
  display: flex;
  align-items: center;
}

.blocked svg {
  display: flex;
  flex-direction: column;

  width: 20.1px;

  margin-right: 11px;
}

.description {
  margin: 19px 0 18px;

  opacity: .5
}

.blockedInfo,
.unlockInfo {
  display: flex;
  justify-content: space-between;

  span:first-of-type {
    opacity: .7;
  }
}

.blockedInfo {
  margin-bottom: 16px;
}

.unlockInfo {
  margin-bottom: 20px;
}

.unlockMobile {
  margin-bottom: -14px;
}

.toggle {
  display: flex;

  color: #fff;

  align-items: center;

  span {
    color: $default-text;
  }
}

.button {
  margin-right: 10px;
}
