@import 'Styles/mixins.scss';

.paymentMethods {
  width: auto;

  padding: 0 15px;
  margin: 0 150px;

  display: flex;
  flex-direction: column;
  align-items: center;

  &.mobileVersion {
    width: 400px;
    max-width: 100%;
    margin: auto;
  }
}

.pageTitle {
  @include page-title;
  padding-bottom: 32px;
}

.button {
  margin-top: 34px;
  display: flex;
  justify-content: center;
  width: 100%;
}
