@import 'Styles/variables.scss';

$modal-title-color: #fe4f67;
$modal-content-bg-color: #29445f;
$modal-mask-bg-color: rgba(#192a3c, .7);

.modal {
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;

  display: flex;
  overflow: auto;
  align-content: center;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 100%;

  background-color: $modal-mask-bg-color;
}

.content {
  position: relative;

  width: 500px;
  max-width: calc(100% - 40px);

  border-radius: 5px;
  background-color: $color-white;

  @media only screen and (min-width: 769px) {
    padding: 65px;
  }

  @media only screen and (max-width: 768px) {
    padding: 35px;
  }
}

.close {
  position: absolute;
  top: 0;
  right: 0;
  outline: none;
  padding: 20px;

  &:hover {
    cursor: pointer;
  }
}

.title {
  margin: 0 0 10px;
  padding: 0;

  color: $modal-title-color;

  font-size: 24px;
  font-weight: normal;
  text-align: center;
}

.text {
  margin: 0 0 25px;
  padding: 0;

  font-size: 14px;
  line-height: 21px;
  text-align: center;
}
