@import '../../../../styles/mixins';


.form {
  padding-bottom: 40px;
}

.fields {
  display: flex;
  justify-content: center;
  align-items: center;
}

.formCreditCard {
  max-width: 370px;
  width: 100%;
  margin-top: 26px;
  margin-right: 26px;
}

.title {
  @include page-title;
  padding-bottom: 26px;

  &:last-of-type {
    margin-top: 50px;
  }
}

.formCreditCard {
  margin: 26px;
}

.formFirstPartFields {
  max-width: 370px;

  @include form-styles(26px);
}

.formFirstPart {
  display: flex;
  justify-content: center;
}

.formSecondsPart {
  display: flex;
  width: 100%;
  justify-content: center;
}

.formSecondsPartWrapper {
  display: flex;
  flex-direction: column;
}

.firstPart,
.secondPart {
  @include form-styles(26px);

  & > :first-child {
    margin-bottom: 52px;
  }

  & > :last-child {
    margin-top: 42px;
  }

  .button {
    width: 175px;
  }
}
