$billing-row-border-color: rgba(58, 83, 108, 0.1);
$row-hover-bg-color: rgba(62, 64, 73, 0.05);

$billing-text-color: #3e4049;

$invoice-color: #0094fe;
$invoice-bg-color: rgba(0, 148, 254, 0.1);
$receipt-color: #00c75f;
$receipt-bg-color: rgba(0, 199, 95, 0.1);

.rowWrapper {
  height: 50px;
  width: 100%;

  cursor: pointer;

  td {
    border-bottom: 1px solid $billing-row-border-color;
  }

  &:hover {
    background-color: $row-hover-bg-color;
  }
}

.date {
  padding-left: 10px;
}

.dateWrapper,
.descriptionWrapper,
.amountWrapper {
  height: 100%;

  display: flex;
  flex-direction: column;
}

.columnName,
.columnValue {
  font-size: 12px;
  color: $billing-text-color;
}

.columnName{
  font-size: 12px;
  color: $billing-text-color;
  opacity: .7;
  margin-bottom: 11px;
}

.columnValue{
  margin-top: 2px;
}

.date {
  font-size: 14px;
  color: $billing-text-color;
}

.typeColumn {
  width: 64px;
  height: 19px;

  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 19px;

  font-size: 10px;

  &.invoice {
    color: $invoice-color;
    background-color: $invoice-bg-color
  }

  &.receipt {
    color: $receipt-color;
    background-color: $receipt-bg-color;
  }
}

.arrowWrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.arrow {
  height: 100%;

  margin-right: 10px;

  display: flex;
  justify-content: center;
  align-items: center;

  svg {
    width: 10px;
    height: 10px;

    transform: rotate(-90deg);

    path {
      fill: #0094fe;
    }
  }
}
