$data-color: #3e4049;
$email-color: #0094fe;

$section-border-color: rgba(58, 83, 108, 0.1);

.sectionWrapper {
  width: 100%;
  display: flex;

  flex-direction: column;

  padding-bottom: 22px;

  border-bottom: 1px solid $section-border-color;
}

.title {
  font-size: 25px;
  color: $data-color;

  margin-bottom: 32px;

  .email {
    color: $email-color;
  }
}

.dataTable {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  margin-top: 23px;
  width: 100%;
}

.rightColumns {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  height: 110px;
}

.column {
  display: flex;
  flex-direction: column;

  justify-content: space-between;

  font-size: 14px;
  color: $data-color;
}

.header {
  opacity: .7;
  margin-bottom: 8px;
}

.value p {
  margin: 0;
  line-height: 23px;
}
