@import 'Styles/variables.scss';


.loading-page {
  background: $main-background;

  &,
  &__mask {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    display: flex;

    align-items: center;
    justify-content: center;
  }

  &__content {
    z-index: 2;
  }

  &__mask {
    background-image: url('../../../img/background.png');
    background-position: center bottom;
    background-size: 100% 100%;
  }
}
