@import 'Styles/variables.scss';

.billing {
  display: flex;
  flex-direction: column;

  width: 370px;
  margin-left: 50px;

  &.isMobile {
    margin-left: 0;

    padding: 0 15px;

    width: 100%;
    max-width: 370px;
  }
}

.names,
.address,
.contacts {
  display: flex;
}

.names,
.address {
  border-bottom: solid 1px rgba(58, 83, 108, .1);
}

.address {
  flex-wrap: wrap;
}

.field {
  width: 100%;
  max-width: 185px;
  margin-top: 19px;
  margin-bottom: 16px;
}

.names .field {
  margin-top: 0;
}

.fieldValue,
.fieldName {
  color: $default-text;
}

.fieldName {
  margin-bottom: 5px;

  opacity: .7;
}

.row {
  display: flex;
  width: 100%;
}
