@import 'Styles/variables.scss';

.footer-wrapper {
  @media print {
    display: none;
  }

  display: flex;
  flex-direction: column;
  background-color: #2b2b2b;
  font-weight: 400;
  min-height: 196px;
  width: 100%;
  margin-top: 100px;

  .copyright {
    display: flex;
    justify-content: center;
    background-color: #222222;
    list-style-type: none;
    margin: 0;
    padding: 0;
    min-height: 57px;

    li {
      background-color: #222222;
      padding: 20px 0;
      color: #fff;
      opacity: .3;
      font-size: 12px;

      @media only screen and (min-width: 631px){
        &:not(:last-of-type):after{
          content: "|";
          margin:0 13px;
        }
      }

      button {
        color: #fff;
        text-decoration: none;
        outline: none;
        border: none;
        background-color: transparent;
        cursor: pointer;
      }
    }

    @media only screen and (max-width: 630px){
      flex-direction: column;
      align-items: center;
      padding-top: 7px;
      padding-bottom: 7px;
      min-height: 111px;


      li {
        padding: 10px 0;
      }
    }
  }
}

.footer {
  display: flex;
  justify-content: space-between;
  padding: 40px 35px;
  background-color: #2b2b2b;

  @media only screen and (min-width: 1250px) {
    width: 1240px;
    margin-left: auto;
    margin-right: auto;
  }

  box-sizing: border-box;
  flex: 1 0 auto;

  a {
    &:hover,
    &:visited,
    &:focus,
    &:link {
      color: rgba(255,255,255, .5);
      text-decoration: none;
    }
  }

  &-block {
    flex-grow: 1;
  }

  &-logo {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    align-items: flex-start;

    &__copyright {
      color: rgba(255,255,255, .5);
    }
  }

  &-nav {
    display: flex;
    flex-direction: column;
    font-size: 15px;

    p,
    a {
      margin: 0 0 9px 0;
    }

    &__title {
      color: #fff;
    }

    &__text {
      color: rgba(255,255,255, .5);
    }
  }

  &-socials {
    display: flex;
    justify-content: flex-end;

    i {
      margin-right: 8px;

      svg {
        min-width: 28px;
        min-height: 28px;
        max-width: 28px;
        max-height: 28px;
      }
    }
  }

  @media screen and (max-width: 992px) {
    flex-direction: column;
    height: auto;

    &-block {
      margin-bottom: 39px;
      width: 250px;
    }

    &-logo {
      width: auto;

      &__copyright {
        margin-top: 20px;
      }
    }

    &-nav {
      order: 4;

      &_feedback {
        margin-top: 8px !important;
      }

      &_sales p {
        line-height: 2em;
      }
    }

    &-logo {
      display: block;
      order: 1;
    }

    &-links {
      order: 2;
    }

    &-socials{
      order: 4;

      margin-bottom: 10px;

      justify-content: flex-start;

      a {
        margin-right: 12px;
      }
    }
  }
}
