@import 'Styles/variables.scss';

.page-nav-header {
  z-index: 45;
  top: 0;
  right: 0;
  left: 0;

  display: flex;
  flex-direction: column;

  color: $color-white;

  justify-content: center;

  &--modal-opened {
    z-index: 25;
  }

  @media only screen and (max-height: 500px) {
    position: absolute;
  }

  @media only screen and (min-height: 501px) {
    position: fixed;
  }

  @media print {
    display: none;
  }

  .container {
    display: flex;
    flex-direction: row;

    width: 100%;
    height: 70px;
    margin: 0 auto;
    padding: 0;

    font-size: $font-size;

    align-items: center;

    @media (max-width: 570px) {
      margin-right: 0;
      padding: 0;
    }
  }

  &__logout {
    cursor: pointer;

    &:hover {
      text-decoration: underline;
    }
  }
}
