@import 'Styles/variables.scss';
@import 'Styles/mixins.scss';


.loginPage {
  display: flex;
  flex-direction: column;

  max-width: 400px;
  margin: auto;
  padding: 0 15px;

  justify-content: center;
}

.title {
  @include page-title;
}

.loginActions {
  display: flex;
  justify-content: space-between;
  margin-top: 22px;
}

.loginLink {
  text-decoration: none;
  color: $blue;

  &:hover {
    text-decoration: underline;
  }
}

.forgotLink {
  color: $default-text;

  &:hover {
    color: $blue
  }
}
