$pricing-block-color: #3e4049;

.pricingWrapper {
  margin-top: 26px;

  width: 100%;
}

.headings,
.product,
.row,
.total {
  display: flex;
  justify-content: space-between;

  color: $pricing-block-color;
}

.headings {
  opacity: .7;

  margin-bottom: 11px;
}

.product {
  margin-bottom: 29px;
}

.row {
  margin-bottom: 7px;
}

.percentage {
  font-size: 10px;

  opacity: .5;
}

.total {
  margin-top: 24px;

  font-weight: bold;
}
