@import 'Styles/variables.scss';

$summary-table-th-bg-color: #233f57;
$summary-table-td-bg-color: #3b516a;
$summary-table-border-color: rgba(#233f57, .2);
$summary-table-none-data-color: #8a99a8;
$td-color: rgb(62, 64, 73);
$th-color: rgb(62, 64, 73);



.summary_table{
  overflow-x: none;
  overflow-y: hidden;
  height: 100%;
  max-width: 100%;
  display: flex;
  flex-flow: column;
  width: 99vw;
  max-height: 100vh - 30;
  border-collapse: collapse;

  padding: 12px 0;
}

.wrapper {
  border-top: 1px solid $summary-table-border-color;
  border-bottom: 1px solid $summary-table-border-color;
  margin-bottom: 10px;
}

.thead {
  display: table;
  width: 100vw - 70;
  overflow: none;
}

.thead .first_table_tr,
.thead .second_table_tr {
  margin: -1.5rem 0;
  padding: 0;
}

.thead .second_table_tr :first-child {
  width: 40vw;
}
.first_table_tr :last-child {
  width: 33vw;
  text-align: end;
}

.first_table_tr :nth-child(2) {
  width: 33vw;
  text-align: center;
}

.second_table_tr :last-child {
  width: 44vw;
  text-align: end;
}

.table_name {
  text-align: left;
  width: 90%;
}

.value {
  opacity: .5;
  text-align: end;
  font-size: 14px;
}

.second_page_value {
  opacity: .5;
  text-align: end;
  font-size: 14px;
}

.td,
.th {
    display: inline-flex;
    padding: 15px 0;
    text-overflow: ellipsis;
    font-weight: normal;
    font-size: 12px;
    text-transform: capitalize;
    color: $td-color;
    width: 33vw;
  }
  
.th {
  text-transform: capitalize;
  opacity: .5;
  margin-bottom: 5px;
}

.icon {
  width: 10px;

  transition: transform .2s;

  svg {
    position: relative;
    top: -3px;
    right: -3px;
    transform: scale(1.2);
    transform: rotate(180deg);
  }
}

.opened {
  width: 10px;

  transition: transform .2s;

  svg {
    position: relative;
    top: -3px;
    right: -3px;
    transform: scale(1.2);
  }
}

.amount,
.first_table_tr,
.second_table_tr {
  display: flex !important;
  width: 90vw;
};

.first_table_tr :nth-last-child(1){
  width: 33vw;
  display: block;
}

.first_table_tr :nth-child(2) {
  width: 33vw;
  display: block;
}

.second_table_tr :nth-last-child(1) {
  margin-left: 33vw;
  display: block;
}

.amount {
    font-size: 16px;
    height: 16px;
};

.none_data {
    color: $summary-table-none-data-color;
};

.tbody {
    display: block;
    overflow-x: none;
    overflow-y: auto;
    flex: 1 1 auto;
}


.second_table_tr:not(:last-of-type),
.first_table_tr:not(:last-of-type) {
    border-bottom: 1px solid $summary-table-border-color;
}
