@import '../../../../node_modules/rc-tabs/assets/index.css';
@import 'Styles/variables.scss';
@import 'Styles/mixins.scss';


.forms-tabs {
  overflow: visible;

  margin-bottom: 20px;

  border-bottom: none;

  &.card-details__mobile {
    width: 370px;
    max-width: 100%;
  }

  .rc-tabs-bar {
    position: relative;
    margin: auto;
    margin-bottom: 34px;
    max-width: 400px;

    border-bottom: none;

    &:after {
      position: absolute;
      bottom: 0;
      width: 100%;
      content: '';
      border-bottom: 2px solid rgba(35, 64, 88, .4);
    }
  }

  .rc-tabs-tabpane-inactive {
    visibility: hidden;
  }

  .rc-tabs-ink-bar {
    background-color: $blue;
    height: 2px;
  }

  .rc-tabs-tab {
    flex: 1 1 0;
    color: $default-text;
    display: flex;

    height: 50px;
    margin-right: 0;

    text-align: center;

    align-items: center;
    justify-content: center;

    &-active {
      color: $blue;
    }

    &:hover {
      color: $blue;
    }
  }

  .rc-tabs-nav {
    width: 100%;

    & > div {
      display: flex;
    }
  }

  .rc-tabs-nav-scroll {
    width: 100%;
  }

  .rc-tabs-content .rc-tabs-tabpane {
    overflow: visible;
  }
}
