.modalWrapper {
  position: fixed;
  z-index: 30;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  display: flex;

  transition: all 400ms;
  background-color: rgba(0,0,0, .4);
  padding: 15px;
  overflow-y: auto;
}


.modal {
  position: relative;

  width: 450px;
  min-height: 300px;
  margin: auto;

  border-radius: 12px;
  background-color: #fff;
  box-shadow: 0 2px 57px 0 rgba(0, 0, 0, .5);
}

.closeBtn {
  position: absolute;
  top: 20px;
  right: 20px;

  cursor: pointer;
  transition: opacity 200ms;

  opacity: .2;

  svg {
    min-width: 14px;
    max-width: 14px;
    min-height: 14px;
    max-height: 14px;

    path {
      transition: fill 200ms;

      fill: #29425c;
    }
  }

  &:hover {
    opacity: .5;

    svg path {
      fill: #fe4f67;
    }
  }
}


.content {
  display: flex;

  padding: 30px 40px 35px 40px;
}
