@import 'Styles/variables.scss';

.user-menu {
  position: relative;
  display: flex;

  margin-left: auto;
  margin-right: 5px;
  padding: 16px;
  width: 214px;
  height: 66px;

  box-sizing: border-box;

  cursor: pointer;

  align-items: center;
  justify-content: space-between;
  transition: all 300ms;
  transition-delay: .2s;

  border-top-left-radius: 9px;
  border-top-right-radius: 9px;
  outline: none;

  @media (max-width: $mobileMediaBrakepoint) {
    height: 70px;
    width: auto;

    border-radius: 0;
    margin-right: 0;
  }

  &--opened {
    transition: background-color 0s;
    background-color: hsla(0, 0%, 100%, .5);

    @media screen and (max-width: $mobileMediaBrakepoint) {
      background-color: transparent;
    }
  }

  &__arrow-icon {
    width: 10px;
    height: 7px;

    transition: transform .2s;

    svg {
      position: relative;
      top: -6px;
      transform: scale(1.2);
    }

    &--opened {
      transform: rotate(180deg);
    }

    @media (max-width: $mobileMediaBrakepoint) {
      display: none;
    }
  }

  &__sandwich-icon {
    display: none;

    margin: 0px 20px;

    @media (max-width: $mobileMediaBrakepoint) {
      display: block;
    }
  }

  .dropdown-menu {
    position: absolute;
    top: 66px;
    right: 0;
    z-index: 5;
    overflow: hidden;
    width: 214px;
    margin: 0;
    padding: 0;

    border-bottom-left-radius: 9px;
    border-bottom-right-radius: 9px;

    transition: max-height .2s linear;
    background-color: rgba(255, 255, 255, .5);

    @media (max-width: $mobileMediaBrakepoint) {
      position: absolute;
      top: 70px;
      right: 0;

      width: 100vw;
      padding: 0;
      background-color: #fff;

      box-shadow: 0 13px 21px 0 rgba(0, 0, 0, 0.08);
    }

    &--closed {
      max-height: 0;
    }

    &--opened {
      max-height: 450px;
      z-index: 25;
    }

    &__image {
      width: auto;
      max-height: 40px;
    }

    &__user-info {
      height: 93px;
      display: flex;
    }
  }

  &__humburger {
    display: none;

    @media (max-width: $mobileMediaBrakepoint) {
      display: block;
    }
  }
}
