@import 'Styles/variables.scss';
@import 'Styles/mixins.scss';


.registration-page {
  max-width: 400px;

  padding: 0 15px;

  width: 100%;
  margin: auto;

  text-align: center;

  color: $text-color;

  align-items: center;
  justify-content: center;

  &__title {
    @include page-title;

    padding-bottom: 20px;

    font-size: 21px;
    font-weight: normal;
  }

  .form-field:not(:last-child) {
    margin-bottom: 25px;
  }
}
