@import 'Styles/variables.scss';
@import 'Styles/mixins.scss';


.page {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;

  background-color: $main-background;

  display: flex;
  flex-direction: column;

  width: 100%;
  min-height: 100%;

  align-items: center;
  justify-content: center;
}

.pageWrapper {
  position: relative;
  z-index: 2;

  display: flex;
  flex-direction: column;
  flex: 1 0 auto;

  width: 100%;

  align-items: center;

  @media (max-width: 400px) {
    width: 100%;
  }
}

.pageTitle {
  @include page-title;
}

.pageContent {
  width: 100%;
  padding-top: 36px;
  flex-grow: 1;
  min-height: 90vh;

  @media screen and (max-height: 500px) {
    min-height: 330px;
  }

  &--privacy {
    max-width: 1000px;
  }

  &--close_account {
    text-align: center;
    max-width: 700px;
  }

  &--registration,
  &--update_account {
    box-sizing: content-box;
  }
}

.pageWeave {
  position: fixed;
  top: 0;
  right: 0;

  width: 100%;

  max-width: 1000px;
  height: 100%;

  background-image: url('../../img/weave.png');
  background-repeat: no-repeat;
  background-size: cover;

  @media (max-width: 480px) {
    background-size: contain;
  }
}

.pageTopColors {
  position: fixed;
  top: 0;
  left: 50%;
  width: 130%;

  transform: translateX(-50%);

  max-width: 2000px;
  height: 1000px;

  background-image: url('../../img/background.png');
  background-size: 100% 100%;
  background-position: top center;
  background-repeat: no-repeat;
}