/* sass-lint:disable extends-before-declarations */
@import 'Styles/variables.scss';

$button-transparent-color: rgba(255, 255, 255, .09);
$button-transparent-hover-color: rgba(255, 255, 255, .18);
$button-indicator-invalid-color: #29445f;
$button-indicator-valid-color: rgb(42, 195, 46);

.inner-input {
  position: absolute;
  top: 0;
  right: 0;

  width: 50px;

  color: $text-color;
  border-radius: 0 5px 5px 0;
}

.button {
  height: 50px;
  width: 100%;

  cursor: pointer;

  border: none;
  border-radius: 10px;
  outline: none;

  font-size: $font-size;

  &.secondary {
    color: $color-white;
    background-color: $button-transparent-color;

    font-size: $font-size;

    &:hover,
    &:focus {
      background-color: $button-transparent-hover-color;
    }
  }

  &.primary {
    color: $color-white;
    background-color: $blue;
    transition: background-color 250ms;

    font-size: $font-size;

    &:hover {
      background-color: lighten($blue, 10%);
    }
  }

  &.inner-input-button {
    &:hover,
    &:focus {
      background-color: $button-transparent-hover-color;
    }
  }

  &.inner-input-indicator  {
    width: 0;
    height: 0;
    background-color: white;
    &:hover {
      cursor: default;
    }

    .inner-button__icon  {
      svg {
        width: 16px;
        position: absolute;
        right: 10%;
        top: 25%;
      }

      path {
        transition: fill .3s ease;
      }

      &--valid {
        path {
          fill: $button-indicator-valid-color;
        }
      }

      &--invalid {
        path {
          fill: $button-indicator-invalid-color;
        }
      }
    }
  }
}

.link-button {
  height: 100%;

  cursor: pointer;
  text-decoration: none;

  color: inherit;
  border: none;
  background-color: transparent;

  &:focus {
    outline: none;
  }
}
