@import 'Styles/variables.scss';

.channelsPage {
  display: flex;

  max-width: 920px;
  margin: auto;
  margin-top: 15px;

  @media screen and (max-width: $mobileWidthEndpoint) {
    display: block;
    flex-direction: column;
    margin-top: 0;

    max-width: 400px;
    padding: 0 15px;

    .column {
      padding: 24px 0 20px 0;
    }

    .column:first-child {
      border-right: none;
      border-bottom: solid 1px rgba(58, 83, 108, .1);
    }
  }
}

.isMobile {
  flex-direction: column;

  align-items: center;
}

.column {
  display: flex;
  flex-direction: column;

  padding: 0 17px;

  flex-basis: 0;
  flex-grow: 1;

  &:first-child {
    border-right: solid 1px #fff;
  }
}

.row {
  display: flex;

  justify-content: space-between;
}

.subscriptionInfo {
  align-items: flex-end;

  @media screen and (max-width: $mobileWidthEndpoint) {
    position: relative;
    top: -8px;
  }
}

.description {
  margin: 0;
  padding-right: 20px;
}

.boldText {
  color: $default-text;

  font-weight: 600;
}

.planInfo {
  display: flex;

  align-items: center;
}

.planRow {
  @media screen and (max-width: $mobileWidthEndpoint) {
    align-items: flex-start;
  }
}

.plan {
  margin-left: 5px;
  line-height: 1.33;
  color: $green;
  letter-spacing: 0.5px;

  font-size: 18px;
  font-weight: 600;
}

.redText {
  color: $red;
}

.description {
  margin-top: 13px;

  color: $default-text;

  line-height: 1.71;

  @media screen and (max-width: $mobileWidthEndpoint) {
    margin-top: 0;
  }
}

.btn {
  display: flex;
  width: 118px;
  height: 40px;

  a {
    height: 40px;
  }
}

.btnEdit {
  display: flex;
  width: 78px;
  height: 40px;

  a {
    height: 40px;
  }
}

.channelsCost {
  text-align: right;

  color: $blue;

  &Amount {
    color: $blue;

    justify-content: flex-end;
  }

  p {
    margin: 0;
    margin-top: 3px;

    white-space: nowrap;
  }
}

.currentChannelsText {
  display: flex;

  color: $default-text;

  font-weight: 600;

  align-items: center;
}

.channelsController {
  display: flex;

  align-items: center;
  justify-content: flex-end;
}

.channelsAmount {
  margin-right: 20px;

  color: $default-text;

  font-size: 28px;
  font-weight: 600;
}

.billingInfo {
  display: flex;
  flex-grow: 1;

  align-items: flex-end;
}

.billingPeriod {
  color: $default-text;

  & > p {
    margin: 0;

    &:first-child {
      font-weight: 600;
    }

    &:last-child {
      margin-top: 3px;

      opacity: .7;
    }
  }
}

.billingAmount {
  display: flex;
  flex-direction: column;

  width: 163px;
  height: 65px;
  margin-top: 13px;

  color: $default-text;
  border: solid 1px #fff;
  border-radius: 9px;

  align-items: center;
  justify-content: center;

  p {
    margin: 0;

    color: $default-text;

    &:first-child {
      font-size: 23px;
      font-weight: 600;
    }

    &:last-child {
      font-weight: 600;
    }
  }
}
