@import './variables';

@mixin page-title {
  color: $default-text;

  font-size: 25px;

  margin: 0;
  padding: 0 0 18px;
  font-weight: 400;

  text-align: center;
}

@mixin page-subtitle {
  color: $default-text;
  font-size: 14px;
  text-align: center;
  line-height: 1.5;
  font-weight: normal;
  margin: 0;

  margin-bottom: 20px;
}

@mixin form-styles($margin) {
  max-width: 370px;
  width: 100%;

  margin: 0 $margin;
}

@mixin page-wrapper {
  width: auto;

  padding: 0 15px;

  display: flex;
  flex-direction: column;
  align-items: center;
}

@mixin mobile-design {
  @media only screen 
    and (min-device-width: 320px) 
    and (max-device-width: 480px)
    and (-webkit-min-device-pixel-ratio: 2)
    and (orientation: portrait) {
      input[type=text] {
        font-size: 21px;
      }
  }

  /* Landscape */
  @media only screen 
    and (min-device-width: 320px) 
    and (max-device-width: 480px)
    and (-webkit-min-device-pixel-ratio: 2)
    and (orientation: landscape) {
      input[type=text] {
        font-size: 21px;
      }
  }

  /* ----------- iPhone 5, 5S, 5C and 5SE ----------- */

  /* Portrait and Landscape */
  @media only screen 
    and (min-device-width: 320px) 
    and (max-device-width: 568px)
    and (-webkit-min-device-pixel-ratio: 2) {
      input[type=text] {
        font-size: 21px;
      }
  }

  /* ----------- iPhone 6, 6S, 7 and 8 ----------- */

  /* Portrait and Landscape */
  @media only screen 
    and (min-device-width: 375px) 
    and (max-device-width: 667px) 
    and (-webkit-min-device-pixel-ratio: 2) { 
      input[type=text] {
        font-size: 23px;
      }
  }
  /* ----------- iPhone 6+, 7+ and 8+ ----------- */

  /* Portrait and Landscape */
  @media only screen 
    and (min-device-width: 414px) 
    and (max-device-width: 736px) 
    and (-webkit-min-device-pixel-ratio: 3) { 
      input[type=text] {
        font-size: 24px;
      }
  }

  /* ----------- iPhone X ----------- */

  /* Portrait and Landscape */
  @media only screen 
    and (min-device-width: 375px) 
    and (max-device-width: 812px) 
    and (-webkit-min-device-pixel-ratio: 3) { 
      input[type=text] {
        font-size: 23px;
      }
  }
}