@import 'Styles/variables.scss';

.wrapper {
  display: flex;

  height: 350px;
  margin-top: 30px;

  background-color: rgba(255,255,255, .8);

  justify-content: center;
}

.section {
  position: relative;

  display: flex;
  flex-direction: column;

  width: 100%;
  max-width: 920px;
  height: 100%;
  margin-top: 32px;
  padding: 0 17px;

  align-items: center;

  @media screen and (max-width: $mobileWidthEndpoint) {
    max-width: 400px;
  }
}

.chart {
  position: relative;
  margin-top: 50px;
  margin-left: -40px;
  width: calc(100% + 60px);
}


.yAxis {
  position: absolute;
  left: 0;
  top: 0px;
  font-size: 10px;
  height: 120px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.borderLine {
  position: absolute;
  width: 100%;
  svg {
    width: 100%;
  }
}

.empty {
  color: $default-text;
  opacity: .7;
  font-size: 24px;
  text-align: center;
}
