@import 'Styles/variables.scss';

.stripe-field {
  display: flex;
  flex-direction: column;

  &.has-error .stripe-card__input {
    border-color: $input-color-error;
  }

  .stripe-label {
    margin-bottom: 9px;

    color: $default-text;
  }

  &__error {
    height: 20px;

    color: $input-color-error;

    font-size: 10px;
  }
}

.stripe-card__input {
  display: flex;

  width: 100%;
  height: 50px;
  padding-top: 0;
  padding-right: 20px;
  padding-left: 20px;

  border: 1px solid transparent;
  border-radius: 9px;
  outline: none;
  background-color: #fff;
  box-shadow: 0 2px 19px 0 rgba(0, 0, 0, .06);

  align-items: center;

  & > * {
    width: 100%;
  }
}
