$toggle-button-off-color: #3e4049;
$toggle-button-on-color: #0094fe;
$color-white: #fff;


.toggle {
  position: relative;

  width: 40px;
  height: 20px;

  cursor: pointer;


  outline: none;

  border-radius: 10px;
  background-color: $color-white;
  box-shadow: 0 2px 19px 0 rgba(0, 0, 0, 0.09);
  padding: 0 16px;

  &::after {
    position: absolute;
    top: 0;
    left: 0;

    width: 20px;
    height: 20px;

    content: '';
    transition: all 150ms;

    border-radius: 50%;
    background-color: $toggle-button-off-color;
  }


  &.isOn::after {
    transform: translateX(20px);

    background-color: $toggle-button-on-color;
  }
}
