@import 'Styles/mixins.scss';
@import 'Styles/variables.scss';


$filter-wrapper-bg-color: rgba(255, 255, 255, 0.53);

.filterWrapper {
  height: 70px;
  width: 100vw;

  display: flex;
  align-items: center;

  margin-top: 22px;
  margin-bottom: 27px;

  background-color: $filter-wrapper-bg-color;


  @media screen and (max-width: 820px) {
    height: 100px;
    position: relative;
  }

  .filterContainer {
    align-self: center;
    flex-direction: row;
  }
}

.filterBtn {
  position: absolute;
  width: calc(100% - 20px);

  bottom: -40px;
  left: 0;
}

.billingTable {
  @include page-wrapper;
  padding: 0;

  &.mobileVersion {
    width: 400px;

    max-width: calc(100vw - 30px);
    margin: auto;
  }
}

.filterContainer {
  width: 100%;
  max-width: 890px;
  margin: auto;
  padding: 0 10px;

  &.mobileVersion {
    position: relative;
    width: 400px;

    max-width: calc(100vw - 30px);

    margin-top: 15px;
  }
}

.pageTitle {
  @include page-title;
  padding-bottom: 32px;
}

.noResults {
  @include page-title;
  opacity: .5;
}
