@import 'Styles/variables.scss';
$text-placeholder: rgba(35, 64, 88, 0.6);
$input-text-color: #3e4049;
$input-border-color-error: #cd5d6c;

.form-field {
  position: relative;

  display: flex;

  width: 100%;

  align-items: flex-start;
  flex-flow: column nowrap;
  justify-content: flex-start;

  margin-bottom: 20px;

  &__icon-wrapper {
    position: absolute;
    top: 0;
    left: 0;

    display: flex;
    flex-direction: column;
    height: 100%;

    justify-content: center;
    align-items: center;

    img {
      max-width: 20px;
      max-height: 20px;
      width: 100%;
      margin: 0 20px;
    }
  }

  &__icon {
    height: 20px;
  }

  ::-webkit-input-placeholder {
    color: $text-placeholder;
  }

  ::-moz-placeholder {
    color: $text-placeholder;
  }/* Firefox 19+ */

  :-moz-placeholder {
    color: $text-placeholder;
  }/* Firefox 18- */

  :-ms-input-placeholder {
    color: $text-placeholder;
  }

  &__label {
    position: relative;
    display: flex;
    margin-bottom: 10px;

    color: $default-text;

    font-family: $font-family;
    font-size: $font-size;

    &_required:after {
      content: '*';
      color: #eb2230;
      position: absolute;
      right: -12px;
      top: -2px;
    }

    &_disabled {
      opacity: .6;
    }
  }

  &__input {
    width: 100%;
    height: 50px;
    padding-top: 0;
    padding-right: 20px;
    padding-left: 20px;

    color: $input-text-color;
    border: 1px solid transparent;
    border-radius: 9px;
    outline: none;

    box-shadow: 0 2px 19px 0 rgba(0, 0, 0, 0.06);

    font-size: $font-size;

    &--with-icon {
      padding-left: 61px;
    }

    &--with-button {
      padding-right: 60px;
    }

    &--disabled {
      pointer-events: none;
      opacity: .6;
    }
  }

  &__error {
    position: absolute;
    bottom: -15px;

    color: $input-color-error;

    font-size: 10px;
  }

  &:hover,
  &:focus {
    .form-field__input {
      border-color: $input-border-color-hover;
    }
  }

  &.has-error {
    .form-field__input,
    .dropdown-control {
      border-color: $input-border-color-error;
    }
  }
}

.form-block__submit {
  position: relative;
}

//prevent default Chrome autocomplete yellow background
input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 50px #fff inset;

  -webkit-appearance: none;
  -webkit-text-fill-color: $input-text-color;
}

input:-internal-autofill-selected {
  background-color: #fff !important;
}
input:-webkit-autofill,
input:-webkit-autofill:focus,
input:-webkit-autofill:focus,
input:-webkit-autofill:hover {
  border: 5px solid #fff !important;
}
