@import 'Styles/variables.scss';

.btn {
  width: 70px;
  height: 35px;
  margin-left: 10px;

  a {
    height: 35px;
  }

  @media screen and (max-width: $mobileWidthEndpoint) {
    width: 117px;
    align-self: flex-end;
  }
}

.btnWrapper {
  flex-grow: 1;
  display: flex;
  justify-content: flex-end;
  margin-right: 6px;
}

.filter,
.part {
  display: flex;
  align-items: center;
  align-self: flex-end;
}

.filter {
  @media screen and (max-width: $mobileWidthEndpoint) {
    flex-direction: column;
    align-items: flex-start;
    align-self: flex-start;
    width: 100%;
  }
}

.text {
  color: $default-text;
  opacity: 0.6;
  font-size: 12px;
}
