@import 'Styles/variables.scss';

.apps-select {
  position: relative;

  display: flex;

  width: 240px;
  height: 70px;
  margin-top: 4px;
  margin-left: 5px;

  cursor: pointer;

  align-items: center;
  justify-content: space-around;

  border-top-left-radius: 9px;
  border-top-right-radius: 9px;

  @media (max-width: 992px) {
    margin-top: 0;
    border-radius: 0;
  }

  @media (max-width: 570px) {
    padding-right: 20px;
    padding-left: 20px;
    margin-left: 0;
    flex-grow: 2;
    justify-content: space-between;
  }

  &--opened {
    background-color: rgba(255,255,255, .5);
    transition: none;
    transition-delay: 0s;

    @media (max-width: 570px) {
      background-color: #fff;
    }
  }

  &__arrow-icon {
    width: 10px;
    height: 7px;

    transition: transform .2s;

    svg {
      position: relative;
      top: -6px;
      transform: scale(1.2);
    }

    &--opened {
      transform: rotate(180deg);
    }
  }

  &__logo-icon {
    max-width: 200px;
    max-height: 60px;
    padding: 0;
    padding-bottom: 6px;

    @media (max-width: 992px) {
      max-height: 80px;
      padding: 20px 0;
    }
  }

  .dropdown-menu {
    position: absolute;
    z-index: 5;
    top: 66px;

    overflow: hidden;

    width: 240px;
    margin: 0;
    padding: 0;

    transition: max-height .2s linear;

    background-color: rgba(255, 255, 255, .5);
    border-bottom-left-radius: 9px;
    border-bottom-right-radius: 9px;

    @media (max-width: 570px) {
      position: absolute;
      left: 0;

      width: 100vw;

      background-color: #fff;
    }

    @media (max-width: 992px) {
      top: 70px;
    }

    &--closed {
      max-height: 0;
    }

    &--opened {
      max-height: 300px;
    }

    &__item {
      list-style-type: none;
    }

    &__image {
      width: auto;
      max-height: 40px;
    }

    &__link {
      display: block;

      height: 70px;
      width: 100%;
      background: transparent;
      border: none;
      padding: 18px 0 18px 18px;

      text-align: left;

      color: $text-color;

      svg {
        max-height: 34px;
        height: 34px;
        max-width: 146px;
        width: 146px;
        float: left;
      }

      &:hover,
      &:focus,
      &:visited,
      &:active {
        outline: none;
        cursor: pointer;
        background-color: rgba(255, 255, 255, .5);
      }
    }
  }
}
