@import 'Styles/variables.scss';


$sliding-button-bg-color: $color-white;
$sliding-button-color: rgba(#8a99a8, .5);
$sliding-button-content-disabled-bg-color: #4b6278;
$sliding-button-content-active-bg-color: #fe4f67;
$sliding-button-icon-disabled-color: #ffffff;
$sliding-button-icon-active-color: #1c3342;

.sliding-button {
  position: relative;

  display: flex;
  overflow: hidden;
  align-items: center;

  width: 200px;
  height: 50px;
  margin: 30px auto 0;

  border-radius: 25px;
  background-color: rgb(250, 250, 250);

  &::after {
    position: absolute;
    left: 55px;

    content: 'Slide to delete';

    color: $sliding-button-color;
  }

  &__wrapper {
    position: absolute;
    width: 45px;
    height: 45px;
    z-index: 1;

    transition: transform .3s ease;

    border-radius: 25px;

    &--disabled {
      &:hover {
        cursor: not-allowed;
      }

      .sliding-button__content {
        background-color: rgba($sliding-button-content-active-bg-color, .5);
      }

      .sliding-button__icon {
        path {
          fill: $sliding-button-icon-disabled-color;
        }
      }
    }

    &--active {
      &:hover {
        cursor: grab;
      }

      .sliding-button__content {
        background-color: $sliding-button-content-active-bg-color;
      }

      .sliding-button__icon {
        path {
          fill: $sliding-button-icon-disabled-color;
        }
      }
    }
  }

  &__content {
    position: absolute;
    right: 0;

    width: 45px;
    height: 45px;

    transition: background-color .3s ease;

    border-radius: 50%;

    &::after {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;

      content: '';
    }
  }

  &__icon {
    position: absolute;
    top: calc(50% - 8px);
    right: calc(50% - 8px);

    svg {
      width: 16px;
    }
  }
}
