@import 'Styles/variables.scss';

.popupWrapper {
    position: relative;
    cursor: help;

    svg {
        max-width: 12px;
        max-height: 12px;
        min-width: 12px;
        min-height: 12px;
        margin-left: 5px;
        margin-top: 3px;
    }

    &:hover .popup {
        opacity: 1;
        transform: translateY(10px);
    }
}

.popup {
    display: flex;
    align-items: center;
    position: absolute;
    z-index: 3;
    bottom: 40px;
    left: -28px;
    opacity: 0;
    transform: translateY(0px);
    pointer-events: none;

    transition: opacity 200ms, transform 300ms;

    padding: 7px;
    border-radius: 3px;
    background-color: #fff;
    border: solid 5px #edeef4;

    box-shadow: 0 5px 10px darken(#edeef4, 10%);
    color: $default-text;
    font-size: 12px;
    line-height: 1.5;

    &:after {
        position: absolute;
        left: 29px;
        bottom: -14px;
        content: '';
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 8.7px 5px 0 5px;
        border-color: #edeef4 transparent transparent transparent;

    }
}

.big {
    width: 200px;
    color: $input-color-error;
}

.small {
    width: 185px;
}
