@import 'Styles/variables.scss';

.wrapper {
  display: flex;
  width: 100%;
  justify-content: flex-end;
  margin-top: 15px;
}
.button {
  width: 117px;
  height: 32px;
  padding-top: 2px;
  margin-right: 6px;
  object-fit: contain;
  border-radius: 9px;
  border: solid 1px rgba(62, 64, 73, 0.3);
  background-color: #ffffff;
  color: $default-text;
  font-size: 12px;
  outline: none;
  cursor: pointer;
}

.isActive {
  border: solid 1px $blue;
  color: $blue;
}
