.edit-account {
  .tab-title {
    position: relative;

    display: flex;
    align-items: center;
    justify-content: center;
  }

  .row {
    display: flex;

    .form-field {
      &:first-child {
        margin-right: 10px;
      }

      &:last-child {
        margin-left: 10px;
      }
    }
  }

  .loading-spinner {
    position: absolute;
    top: 50%;
    left: 0;
    height: 150px;
  }
}
