@import 'Styles/variables.scss';

.restore-pass-form {
  max-width: 400px;
  padding: 0 15px;
  width: 100%;
  margin: auto;
}

.restore-pass-form__links {
  display: flex;
  justify-content: space-between;
  margin-top: 22px;

  a {
    text-decoration: none;
    color: $blue;

    &:hover {
      text-decoration: underline;
    }
  }
}
