@import 'Styles/variables.scss';

.checkboxes .has-error .checkboxes__icon {
  border: solid 1px #fe4f67;
}

.pbx-checkbox {
  display: flex;

  &.disabled {
    opacity: .6;
    cursor: not-allowed;

    a,
    label,
    input[type="checkbox"] + .checkboxes__icon {
      cursor: not-allowed;
    }
  }

  input[type="checkbox"] {
    width: 0;
    visibility: hidden;
    margin: 0;
    padding: 0;
  }

  a,
  label {
    cursor: pointer;
    margin-bottom: 0;
    margin-top: 2px;
    color: $default-text;
  }

  a:hover {
    color: #0094fe;
  }

  input[type="checkbox"] + .checkboxes__icon {
    background-color: #3e4049;
    display: inline-block;
    height: 18px;
    margin: 0 .25em 0 0;
    padding: 0;
    vertical-align: top;
    width: 18px;
    margin-right: 10px;
    border-radius: 5px;
    text-align: center;
    cursor: pointer;

    svg {
      position: relative;
      top: 1px;
      opacity: 0;
      max-width: 9px;
      max-height: 9px;


      path {
        fill: #fff;
      }
    }
  }
  input[type="checkbox"]:checked + .checkboxes__icon svg{
    opacity: 1;
  }

  .form-field__input {
    height: 0;
    margin-bottom: 10px;
  }
}
