@import 'Styles/variables.scss';
@import 'Styles/mixins.scss';

$privacy-page-paragraph-color: #b4c2d2;

.privacy-policy-table {
  border: 1px solid white;
  border-collapse: collapse;
  color: #25435c;

  thead>tr>th, thead>tr>td, tbody>tr>th, tbody>tr>td, tfoot>tr>th, tfoot>tr>td {
    border: 1px solid #25435c;
  }

  td {
    padding: 10px;
  }

  &__wrapper {
    width: 100%;
    overflow: scroll;
  }
}

.privacy_policy {
  box-sizing: border-box;
  color: #25435c;

  width: auto;
  max-width: 800px;

  padding: 0 15px;
  margin: 0 auto;

  h3 {
    font-size: 18px
  }

  p {
    color: #25435c;
    font-weight: 300;
    line-height: 24px;
    text-align: left;
  }

  b {
    color: #25435c;
    font-weight: bold;
  }

  a {
    color: #25435c;
    text-decoration: none;

    &:hover,
    &:visited,
    &:focus,
    &:active {
      color: #25435c;
      text-decoration: none;
    }
  }

  li {
    color: #25435c;
  }

  ul {
    ul {
      list-style-type: disc;
    }
  }

  .background_image {
    background-size: auto;
  }
}

ul.privacy-policy-ul {
  list-style: none;
  color: #25435c;

  li {
    line-height: 24px;
  }
  p {
    padding: 0;
    margin: 0;
  }
}

.privacy {
  .top-slide {
    background-size: 420px;
    background-position: right 35px;
  }
  .privacy-last-update {
    padding-bottom: 20px;
  }
  .privacy-policy-list {
    li {
      margin-bottom: 40px;
    }
  }
}

.privacy-policy-list {
  list-style: none;
  padding: 0;
  margin: 0;
  padding: 0 15px;

  h3 {
      font-size: 24px;
      text-align: left;
      color: #25435c;
  }
  h4 {
    font-size: 18px;
    text-align: left;
    color: #25435c;
  }
  p {
    color: #25435c;
    font-weight: 300;
    margin-bottom: 35px;
    line-height: 24px;
    text-align: left;
  }
}
