$data-color: #3e4049;
$email-color: #0094fe;

$section-border-color: rgba(58, 83, 108, 0.1);

.sectionWrapper {
  width: 100%;
  display: flex;

  padding-bottom: 26px;

  border-bottom: 1px solid $section-border-color;
}

.recieptWrapper {
  display: flex;
  flex-direction: column;

  width: 100%;
}

.title {
  font-size: 25px;
  color: $data-color;

  margin-bottom: 26px;

  .email {
    color: $email-color;
  }
}

.dataTable {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.column {
  display: flex;
  flex-direction: column;

  justify-content: space-between;

  font-size: 14px;
  color: $data-color;
}

.header {
  opacity: .7;
  margin-bottom: 8px;
}
