@import 'Styles/variables.scss';
@import 'Styles/mixins.scss';


.confirmation-page {
  display: flex;
  flex-direction: column;

  max-width: 420px;
  margin: auto;

  color: $default-text;

  align-items: center;

  &__title {
    @include page-title;
  }

  &__details {
    @include page-subtitle;
    max-width: 340px;
  }
}
