@import 'Styles/variables.scss';

$summary-table-th-bg-color: #233f57;
$summary-table-td-bg-color: #3b516a;
$summary-table-border-color: rgba(#3a536c, .1);
$summary-table-none-data-color: #8a99a8;
$td-color: rgb(62, 64, 73);
$th-color: rgb(62, 64, 73);

.summary_table{
  overflow-x: none;
  display: inline-flex;
  flex-flow: column;
  width: 50%;
  height: 100vh - 65;

  ::-webkit-scrollbar {
    width: 5px;
  }

  ::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: $blue;
  }

  ::-webkit-scrollbar-track {
    background-color: $main-background;
  }
  border-collapse: collapse;
}

.td,
.th {
  display: inline-flex;
  text-overflow: ellipsis;
  font-weight: normal;
  font-size: 12px;
  text-transform: capitalize;
  color: $td-color;
}

.th {
  opacity: .5;
  margin-bottom: 5px;
}

.amount,
.second_table_tr,
.first_table_tr {
  display: flex !important;
  margin: 0 15px;
  padding: 15px 0;
};

.first_table_tr :nth-last-child(n){
  text-align: center;

  width: 33vw;
  display: block;
  margin: 0;
}

.first_table_tr :first-child {
  text-align: start;
}

.first_table_tr :last-child {
  text-align: end;
}

.second_table_tr :nth-child(n) {
  width: 50vw;
  display: block;
  text-align: end;
  margin: 0;
}

.second_table_tr :first-child {
  text-align: start;
}

.amount {
  font-size: 16px;
  padding: 0;
};

.tableName {
  text-align: left;
  width: 50%;
}

.value {
  opacity: .5;
  text-align: end;
  width: 50%;
  font-size: 14px;
}

.tbody {
  display: block;
  overflow-x: none;
  overflow-y: auto;
  flex: 1 1 auto;
}

.second_table_tr:not(:last-of-type),
.first_table_tr:not(:last-of-type) {
  border-bottom: 1px solid $summary-table-border-color;
}

.thead .first_table_tr,
.thead .second_table_tr {
  padding: .75rem 0 .25rem 0;
}