.banner-cookies {
  &__wrapper {
    top: 0px;
    z-index: 1001;

    display: flex;

    box-sizing: border-box;
    width: 100%;
    padding: 9px 20px;

    background-color: #56B376;

    align-items: center;
    justify-content: center;
  }

  &__text {
    margin: 0;
    margin-right: 20px;

    color: white;

    font-family: 'Open Sans';
    font-size: 12px;
  }

  &__link {
    text-decoration: underline;

    color: #fff;

    &:hover,
    &:visited,
    &:active,
    &:focus {
      text-decoration: underline;

      color: #fff;
    }
  }

  &__btn {
    height: 22px;
    padding: 0 14px;

    cursor: pointer;

    color: #505e81;
    border-width: 0;
    border-radius: 3px;
    background-color: #fff;

    font-size: 12px;
    line-height: 22px;
  }
}
