@import 'Styles/variables.scss';
@import '../node_modules/normalize.css';


body {
  min-width: 320px;

  font-family: $font-family;
  font-size: 14px;
}

* {
  box-sizing: border-box;
}

input[type="color"],
  input[type="date"],
  input[type="datetime"],
  input[type="datetime-local"],
  input[type="email"],
  input[type="month"],
  input[type="number"],
  input[type="password"],
  input[type="search"],
  input[type="tel"],
  input[type="text"],
  input[type="time"],
  input[type="url"],
  input[type="week"],
  select:focus,
  textarea {

  @media only screen and (max-device-width : 480px) {
    font-size: 16px;
  }
}
