@import 'Styles/variables.scss';

.tab-form {
  &__navigation {
    text-decoration: none;
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: center;

    &-item {
      width: 6px;
      height: 6px;
      border-radius: 6px;
      margin: 0px 2px;
      background-color: lighten($default-text, 30%);

      &_active {
        background-color: #0094fe;
      }
    }
  }
}

.form__buttons-wrapper {
  display: flex;
  justify-content: center;
  margin-top: 25px;
  width: 100%;

  .small-button {
    max-width: 175px;
    width: 100%;

    &:first-child {
      margin-right: 10px;
    }

    &:last-child {
      margin-left: 10px;
    }
  }
}
