@import 'Styles/variables.scss';

.remove-item-button {
  position: relative;

  display: inline-flex;

  box-sizing: border-box;
  height: 22px;
  margin: 0 auto;
  margin-top: 30px;
  padding: 0;
  padding-top: 15px;

  transition: background-color .3s ease;

  color: $default-text;

  align-items: center;
  flex-flow: row nowrap;
  flex-shrink: 0;
  justify-content: center;

  &--disabled {
    opacity: .6;
    pointer-events: none;
  }

  &::before {
    position: absolute;
    top: 0;
    left: 50%;

    width: 150px;
    height: 1px;

    content: '';
    transform: translateX(-50%);

    background-color: $default-text;
  }

  &:hover {
    cursor: pointer;
  }

  &__icon {
    width: 12px;
    height: 12px;
    margin-right: 12px;

    transform: translateY(-1px);

    g {
      fill: #fe4f67;
    }
  }
}
