@import 'Styles/variables.scss';

$page-background-top: #293e59;
$page-background-bottom: #294763;

$button-primary-bg-color: $color-white;

.not-found {
  background: linear-gradient(to top, $page-background-top, $page-background-bottom);

  &,
  .table {
    position: absolute;
    top: 0;
    left: 0;

    display: table;

    width: 100%;
    height: 100%;
  }

  .table {
    background-image: url('../../img/background.png');
    background-position: center bottom;
    background-size: 100% 100%;
  }

  .table-row {
    display: table-row;
  }

  .table-cell {
    display: table-cell;

    text-align: center;
    vertical-align: middle;

    .container {
      box-sizing: border-box;
      max-width: 1000px;
      margin: 0 auto;
      padding: 15px;
    }
  }

  .logo-wrapper {
    margin-bottom: 50px;
  }

  .logo-wrapper img {
    width: 147px;
    height: auto;
  }

  .error-image-wrapper {
    padding: 40px 0 35px;

    h2 {
      margin-bottom: 20px;
      padding-bottom: 0;

      color: $color-white;

      font-size: 24px;
      font-weight: normal;
    }

    p {
      margin: -10px 0 35px;
      padding: 0;

      color: $color-white;
    }

    a {
      display: inline-block;

      width: 200px;
      height: 50px;

      transition: background-color .3s ease;
      text-decoration: none;

      color: $button-primary-text-color;
      border-radius: 5px;
      background-color: $button-primary-bg-color;

      line-height: 50px;

      &:hover,
      &:focus {
        background-color: $button-primary-bg-color;
      }
    }
  }

  .error-image-wrapper img {
    width: 115px;
    height: auto;
  }
}
