@import 'Styles/variables.scss';

.dropdownWrapper {
  position: relative;

  color: $default-text;
  width: 83px;

  font-size: 12px;
}

.selected {
  position: relative;

  box-sizing: border-box;
  height: 32px;
  padding: 0;
  padding-right: 30px;
  padding-left: 11px;

  cursor: pointer;
  text-align: left;
  white-space: nowrap;

  color: $default-text;
  border: solid 1px #fff;
  border-radius: 9px;
  outline: none;
  background-color: #fff;

  line-height: 32px;

  &::after {
    position: absolute;
    top: 13.5px;
    right: 11px;

    width: 8px;
    height: 5.5px;

    content: '';
    transition: transform 150ms;

    background-image: url('../../../../img/dropdown-arrow.svg');
    background-size: cover;
  }

  &.focused {
    &::after {
      transform: rotate(180deg);
    }
  }
}

.focused {
  border-color: $blue;
}

.dropdown {
  position: absolute;
  z-index: 2;
  top: 36px;

  display: none;
  overflow-y: auto;
  flex-direction: column;

  width: 100%;
  max-height: 120px;
  margin: 0;
  padding: 0;

  list-style-type: none;

  border-radius: 4px;
  background-color: #fff;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, .1);

  &.show {
    display: flex;
  }
}

.option {
  padding: 5px 7px;

  cursor: pointer;

  &:hover {
    background-color: rgba(0, 0, 0, .1);
  }
}
