@import 'Styles/variables.scss';

.notification {
  &-container-top-right {
    top: 106px;
    z-index: 40;
  }

  &-container-bottom-right {
    top: 146px;
    z-index: 40;

  @media only screen 
    and (min-device-width: 320px) 
    and (max-device-width: 568px) {
      top: 239px;
  }

  @media only screen 
    and (min-device-width: 375px) 
    and (max-device-width: 812px) { 
      top: 194px;
    }
  }

  &-container-top-center {
    top: 110px;
    z-index: 40;
    left: 1282px;
  }

  &-item {
    border-radius: 10px;
  }

  &__wrapper {
    width: 340px;
    border-radius: 10px;
    display: flex;
    overflow: hidden;
  }

  &_error {
    background-color: #fe4f67;
  }

  &_success {
    background-color: $green;
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(255, 255, 255, .46);
    width: 40px;
    flex-shrink: 0;

    svg {
      max-width: 20px;
      max-height: 20px;
      min-width: 20px;
      min-height: 20px;
    }
  }

  &__message {
    padding: 11px 22px;
    box-sizing: border-box;
    color: #fff;
    line-height: 1.5;

    a {
      color: #fff;

      &:hover {
        color: #fff;
      }
    }
  }
}

@keyframes fadeInLeftBig {
  from {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

.fadeInLeftBig {
  animation: fadeInLeftBig 400ms;
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

@keyframes fadeInRightBig {
  from {
    opacity: 1;
    transform: translate3d(0%, 0, 0);
  }

  to {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }
}

.fadeInRightBig {
  animation: fadeInRightBig 400ms;
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}