@import 'Styles/variables.scss';

$item-hover-bg-color: rgba(62, 64, 73, 0.05);
$payment-text-color: #3e4049;
$card-border-color: rgba(58, 83, 108, 0.1);
$card-arrow-color: #0094fe;
$blocked-text-color: #fe4f67;


.title {
  font-size: 21px;
  color: #fff;
  text-align: center;
  margin-bottom: 40px;
}


.description {
  margin-bottom: 30px;

  line-height: 1.5;

  color: $payment-text-color;
}

.list {
  display: flex;
  flex-direction: column;

  margin: 0;
  padding: 0;

  list-style-type: none;
}

.listItem {
  position: relative;
  z-index: 2;

  display: flex;

  height: 53px;

  color: $payment-text-color;
  border-bottom: solid 1px $card-border-color;

  align-items: center;

  &:hover {
    background-color: $item-hover-bg-color;
  }
}

.drag {
  width: 50px;

  padding: 18px 0;

  cursor: pointer;
}

.body {
  display: flex;
  align-items: center;

  flex-grow: 1;

  cursor: default;

  height: 100%;
}

.extraSpace {
  .brand {
    min-width: auto;
    svg {
      max-width: none;
      margin-left: 10px;
    }
  }
}

.clickable {
  cursor: pointer;
}

.dragIcon {
  pointer-events: none;
  height: 20px;
  margin-left: 10px;

  svg {
    position: relative;
    top: -2px;

    min-width: 12px;
    max-width: 12px;
    min-height: 7px;
    max-height: 7px;

    cursor: pointer;

    & path {
      fill: #405972;
    }
  }
}

.brand {
  min-width: 99px;

  svg {
    display: flex;
    flex-direction: column;

    width: 30px;
    margin: 0 32px 0 37px;
  }
  @media screen and (max-width: $mobileWidthEndpoint) {
    min-width: 71px;

    svg {
      margin-left: 29px;
      margin-right: 12px;
    }
  }
}

.blocked {
  color: $blocked-text-color;

  .blockedIcon {
    margin-left: auto;
    margin-right: 18px;

    @media screen and (max-width: $mobileWidthEndpoint) {
      margin-right: 10px;
    }
    
    svg {
      display: flex;
      flex-direction: column;

      min-width: 20px;
      max-width: 20px;
      min-height: 20px;
      max-height: 20px;
    }
  }
}

.buttons {
  display: flex;
  height: 100%;
  margin-left: auto;
  align-items: center;
  cursor: pointer;
}

.arrow {
  margin: 0 10px;

  svg {
    path {
      fill: $card-arrow-color;
    }
    width: 10px;
    height: 10px;

    transform: rotate(-90deg);
  }
}
