@import 'Styles/variables.scss';

.userInfoWrapper {
  display: flex;
  width: 100%;
  height: 93px;

  @media screen and (max-width: $mobileMediaBrakepoint) {
    display: none;
  }
}

.forMobile {
  padding: 0 22px;
  display: none;

  @media screen and (max-width: $mobileMediaBrakepoint) {
    display: flex;
    border-bottom: solid 1px #f3f3f3;
  }
}

.infoWrapper {
  display: flex;
  align-items: center;
  width: 100%;
}

.name {
  width: 115px;
  line-height: 1.3;
  color: $default-text;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-right: 12px;
  white-space: nowrap;
  font-weight: 600;

  @media screen and (max-width: $mobileMediaBrakepoint) {
    width: auto;
  }
}

.avatar {
  display: flex;
  width: 30px;
  height: 30px;
  margin-right: 15px;
  text-align: center;
  border-radius: 50%;
  background-color: $blue;
  font-size: 11px;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
}


.plan {
  margin: 0;
  color: $green;
  font-size: 12px;
  font-weight: 600;
  display: inline-block;
  margin-top: 5px;
}

.text {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
