@import 'Styles/variables.scss';
@import 'Styles/mixins.scss';


.update-password-page {
  display: flex;
  flex-direction: column;

  max-width: 370px;
  margin: auto;

  color: $text-color;

  align-items: center;
  justify-content: flex-start;

  &__title {
    @include page-title;
  }

  &__description {
    @include page-subtitle;
    margin-bottom: 30px;
  }
}
