.modal-enter {
  opacity: 0;
  .anim-modal {
    transform: translateY(100px);
  }
}

.modal-enter-active {
  transition: all 300ms;

  opacity: 1;
  overflow: hidden;

  .anim-modal {
    transition: all 300ms;
    transform: translateY(0);
  }
}

.modal-exit {
  opacity: 1;

  .anim-modal {
    transform: translateY(0);
  }
}

.modal-exit-active {
  transition: all 300ms;

  opacity: 0;

  .anim-modal {
    transition: all 300ms;
    transform: translateY(100px);
  }
}
