@import 'Styles/variables.scss';

.credit-card-temp {
  height: 220px;
  max-width: 370px;
  width: 100%;
  padding: 30px 26px 20px 26px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  transition: background-color 200ms;
  border-radius: 22px;

  background-color: #fff;
  box-shadow: 0 2px 19px 0 rgba(0, 0, 0, 0.06);

  &__description {
    margin: 0;

    text-align: center;

    color: $default-text;

    line-height: 1.5;
  }

  &_selected {
    display: flex;
    flex-direction: column;

    background-color: #fff;

  }

  &__logo {
    display: flex;

    height: 56px;
    margin-bottom: 34px;

    justify-content: flex-end;

    svg {
      width: 90px;
      height: 56px;
    }
  }

  &__brands {
    position: relative;
    top: -7px;
    display: flex;

    margin: 0;
    padding: 0;

    list-style-type: none;

    justify-content: center;

    @media screen and (max-width: 370px){
      flex-wrap: wrap;
    }

    &-item {
      margin: 0 5px;

      svg {
        width: 30px;
      }
    }
  }

  &__number,
  &__name,
  &__date {
    height: 20px;

    color: $default-text;
  }

  &__field {
    height: 20px !important;
    border-radius: 20px;
    background-color: rgba(62, 64, 73, .1);
  }

  &__number {
    margin-bottom: 20px;

    font-size: 25px;
    display: flex;
    justify-content: space-between;

  }

  &__wrap {
    display: flex;
    height: 25px;

    justify-content: space-between;
  }

  &__date {
    width: 60px;
    height: 25px;
    text-align: right;
    line-height: 25px;

    font-size: 20px;
  }

  &__name {
    overflow: hidden;
    height: 25px;
    width: 100%;
    max-width: 240px;

    line-height: 25px;

    white-space: nowrap;
    text-overflow: ellipsis;

    font-size: 20px;
  }
}
