.table {
  width: 100%;
  max-width: 890px;
  table-layout: fixed;
}

.description,
.amount,
.th {
  font-size: 12px;
  font-weight: 400;

  text-align: left;

  color: #3e4049;

  opacity: .7;

  &.dateHeader {
    padding-left: 10px;
  }
}

.th {
  width: 100px;
}

.description {
  width: auto;
}

.amount {
  width: 100px;
}

.arrow {
  width: 40px;
}

.mobile {
  .th {
    width: 80px;

    @media screen and (max-width: 350px) {
      width: 72px;
    }
  }

  .amount {
    width: 70px;
  }

  .arrow {
    width: 20px;
  }
}
