@import 'Styles/variables.scss';


.close_account_page {
  color: $default-text;
  max-width: 800px;
  margin: auto;
}

a {
  text-decoration: none;
}

.title {
  font-weight: normal;
  text-align: center;
  font-size: 16px;
  height: 22px;
  padding: 0;
}

.mainTitle {
  font-weight: normal;
  text-align: center;
  font-size: 25px;
  height: 34px;
  padding: 0;
}

.description {
  line-height: 21px;
  margin: auto;
  text-align: center;
  max-width: 50vw;
}

.summary_title {
  font-size: 16px;
  font-weight: normal;
  margin-bottom: 20px;
  text-align: center;
}

.buttons_wrapper {
  margin: 2.5rem auto .5rem;
  align-items: center;
  display: flex;
  justify-content: center;
}

.button {
  min-height: 50px;
  max-width: 175px;
  width: 100%;
  margin-left: 20px;
}

.link {
  margin: 0 .5rem;
  min-height: 50px;
  text-decoration: none;
}

