@import 'Styles/variables.scss';
@import '../../../../styles/mixins';

.red {
  color: $red;
}

.upgradePlan {
  display: flex;
  flex-direction: column;

  align-items: center;
  color: $default-text;
}

.button {
  margin-top: 22px;
  width: 100%;
}

.planChange {
  text-transform: capitalize;
  display: flex;
  text-align: center;
}

.planFrom,
.planTo {
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 600;
  color: $green;
}

.planFrom:after {
  content: '\25b6';
  color: #d8d8d8;
  font-size: 12px;
  margin: 0 8px;

  position: relative;
  top: -2px;
}

.warning {
  font-size: 12px;
  line-height: 1.5;
  color: #fe4f67;
  text-align: center;
  margin-top: 18px;
  margin-bottom: 0;
}

.description {
  font-size: 12px;
  line-height: 1.5;
  text-align: center;
  margin: 21px 0;
}

.channelsControl {
  border-top: solid 1px #e1e1e1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.channelsText {
  font-size: 18px;
  margin-bottom: 0;
  margin-top: 21px;
}

.channelsSelection {
  display: flex;
  align-items: center;
  @include mobile-design;
}

.channelsAmount {
  width: 80px;
  height: 46px;
  box-shadow: 0 2px 19px 0 rgba(0, 0, 0, 0.06);

  border-radius: 9px;

  font-size: 28px;
  font-family: $font-family;
  color: #192b40;
  font-weight: 600;

  margin: 25px 20px;

  text-align: center;

  line-height: 46px;
  border: none;
  outline: none;
}

.channelsPlus,
.channelsMinus {
  width: 34px;
  height: 34px;
  border-radius: 50%;
  background-color: #0094fe;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 150ms;
  outline: none;
  border: none;

  &:hover,
  &:active {
    background-color: lighten(#0094fe, 5%);
  }

  svg {
    max-width: 18px;
    min-width: 18px;
  }
}

.channelsMinus svg {
  position: relative;
  top: -3px;
}

.channelsPlus svg {
  position: relative;
  top: 2px;
}

.disabledBtn {
  cursor: not-allowed;
  opacity: .6;
}

.proratedCost {
  font-size: 23px;
  font-weight: 600;
}

.proratedText {
  font-size: 14px;
  font-weight: 600;
}

.billingPeriod {
  opacity: .5;
}

.billingPeriod {
  margin-top: 2px;
}

.vat {
  display: flex;
  position: relative;
  align-items: center;
  margin-top: 18px;
  margin-bottom: 3px;
}

.vatpopup {
  position: absolute;
  left: -22px;
}

.total {
  font-weight: 600;
}

.nextBilling {
  font-size: 12px;
  opacity: .5;
  margin-top: 16px;
  margin-bottom: 4px;
}
