$section-border-color: rgba(58, 83, 108, 0.1);

.infoWrapper {
  position: relative;

  display: flex;
  flex-direction: column;

  justify-content: center;

  padding: 0 26px;
  margin-right: 28px;

  width: 180px;
  height: 167px;

  background-color: rgba(255, 255, 255, 0.44);
  border-radius: 9px;

  &.mobile {
    width: 100%;
  }

  div {
    color: #3e4049;
    text-align: left;
  }

  a {
    color: #0094fe;
    outline: none;
    text-decoration: none;
  }

  div, a {
    line-height: 23px;
    font-size: 12px;
  }
}

.copyIcon {
  position: absolute;
  right: 10px;
  top: 10px;

  cursor: pointer;

  svg {
    width: 11px;
    height: 12px;
  }
}

@media print {
  .infoWrapper {
    border-right: 1px solid $section-border-color;
    border-radius: 0;
  }
  .copyIcon {
    display: none;
  }
}
