@import 'Styles/variables.scss';

.section--registration {
  margin-bottom: 20px;

  &__subtitle {
    margin: 0 0 20px 0;
    font-weight: normal;
    color: $default-text;
  }

  .form__buttons-wrapper {
    display: flex;

    height: 50px;
    margin-top: 30px;

    justify-content: space-between;

    .button {
      width: 100%;
    }

    .button + .button {
      margin-left: 15px;
    }
  }
}

.dots {
  display: flex;

  height: 10px;
  margin-bottom: 20px;

  align-items: center;
  justify-content: center;
}

.dot {
  left: 3px;

  width: 6px;
  height: 6px;
  margin-right: 3px;

  border-radius: 50%;
  background-color: #878a99;

  &--active {
    background-color: $blue;
  }
}

.adress__fields {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  .form-field {
    width: calc(50% - 10px);
    text-align: left;
  }
}

.fourth-registration-step {
  display: flex;
  flex-direction: column;
}
