@import 'Styles/variables.scss';


.close_account_page {
  color: $default-text;
  max-width: 100vw - 10;
  margin: auto;
}

a {
  text-decoration: none;
}

.title {
  font-weight: 400;
  text-align: center;
  font-size: 25px;
  margin: 0 0 30px;
  padding: 0;
}

.description {
  font-size: 14px;
  line-height: 1.5px;
  margin: auto;
  text-align: center;
  max-width: 100vw - 10;
}

.summary_title {
  font-size: 16px;
  height: 22px;
  font-weight: normal;
  margin-bottom: 2.25rem;
  text-align: center;
  width: 100vw - 9;
}

.buttons_wrapper {
  margin: 2rem auto 0;
  align-items: center;

  display: flex;

  max-width: 100vw - 10;
}

.title {
  font-weight: normal;
  text-align: center;
  font-size: 16px;
  padding: 0;
}

.mainTitle {
  font-weight: normal;
  text-align: center;
  font-size: 25px;
  padding: 0;
}

.button {
  min-height: 50px;
  width: 100%;

  margin-left: 15px;
}

.link {
  margin-left: 7vw;
  min-height: 50px;
  text-decoration: none;
}