@import '../../../../styles/mixins';
@import 'Styles/variables.scss';

.pageWrapper {
  @include page-wrapper;

  &.mobileVersion {
    width: 400px;
    max-width: calc(100vw - 30px);
    margin: auto;

    @media print {
      margin-top: -13rem;
      outline: 15rem #fff;

      .printedArea {
        padding: 2rem;
        width: 186mm;
        height: 253mm;
        background-color: $color-white;
        z-index: 999;
      }
    }
  }

  @media print {
    margin-top: -15vw;

    @include mobile-design;
    
    .printedArea {
      width: 192mm;
      height: 279mm;
      background-color: $color-white;
      z-index: 999;
    }

    .buttonsWrapper {
      display: none;
    }
  }
};

.buttonsWrapper {
  width: 370px;
  max-width: calc(100vw - 30px);

  margin: 56px 0;

  display: flex;
  flex-direction: column;

  align-items: center;
}

.backBtn {
  width: 100%;
  max-width: 370px;

  margin-top: 21px;
}

.printedArea {
  width: 100%;
  max-width: 800px;
}
