@import 'Styles/variables.scss';

.dropdown {
  &-container {
    width: 100%;
    height: 50px;
    padding-top: 0;
  }

  &__control {
    display: flex;

    width: 100%;
    height: 50px;

    color: $input-text-color;
    border: 1px solid transparent !important;
    border-radius: 9px;
    outline: none;
    background-color: $input-border-color-hover !important;

    font-size: $font-size;

    align-items: center;

    &--with-icon {
      padding-left: 61px;
    }

    &--with-button {
      padding-right: 60px;
    }

    &:hover,
    &:focus {
      border-color: $input-border-color-hover;
    }
  }

  &__indicator-separator {
    display: none;
  }

  &__menu {
    border: none;
    border-bottom-right-radius: 4px;
    border-bottom-left-radius: 4px;
    background-color: #3a536c !important;

    .Dropdown-option {
      color: #3e4049;

      &.is-selected {
        background-color: #3a536c;
      }

      &:hover {
        background-color: #3a536c;
      }
    }
  }
}
