@import 'Styles/variables.scss';

.notification {
    z-index: 10;
    width: 100%;
    color: #fff;
    padding: 8px 20px;
    background-color: $blue;
    display: flex;
    align-content: center;

    i {
        display: flex;
        align-items: center;
    }

    svg {
        width: 22px;
        height: 20px;
    }
}

.text {
    display: inline-block;
    margin-left: 12px;
    line-height: 19px;
}

.btn {
    text-decoration: underline;
    cursor: pointer;
    margin-left: 5px;
}
