@import 'Styles/variables.scss';

.selectPaymentMethod {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 100%;
}

.errorTitle {
  color: #fe4f67;
  font-size: 18px;
  font-weight: 600;
  padding-top: 10px;
}

.title {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.titleText {
  color: $green;
  font-size: 18px;
  font-weight: 600;
  padding-top: 8px;
}

.emptyCard {
  width: 253px;
  height: 150px;
  object-fit: contain;
  margin: 35px 0;

  &Text {
    line-height: 1.43;
    color: $default-text;
    margin: 0;
    margin-bottom: 30px;
  }
}

.titleDescription {
  margin-top: 15px;
  font-size: 12px;
  line-height: 1.67;
  color: $default-text;
}

.creditCard {
  transition: background-color 200ms;
  border-bottom: none;
  padding: 0 10px;

  &:hover {
    background-color: #f5f8f8;
  }
}

.creditCardList {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.button {
  margin-top: 22px;
  width: 100%;
}
