@import 'Styles/variables.scss';

$button-secondary-bg-color-hover: #192b40;
$button-secondary-bg-color-pressed: #192b40;
$button-secondary-text-color: $color-white;

.button {
  display: flex;

  box-sizing: border-box;
  width: 100%;
  max-width: 370px;
  height: 50px;
  padding: 0;

  cursor: pointer;
  transition: background-color .3s ease;

  border: none;
  border-radius: 9px;

  font-size: $font-size;
  font-weight: normal;
  line-height: 16px;

  align-items: center;
  justify-content: center;

  svg {
    width: 30px;
    height: 30px;
  }
}

.isDisabled {
  cursor: not-allowed;

  opacity: .6;
}

.primary {
  color: $button-primary-text-color;
  background-color: $blue;

  .moving {
    stroke: $button-primary-text-color;
  }

  &:hover {
    background-color: lighten($blue, 15%);
  }

  &:focus,
  &:active {
    background-color: lighten($blue, 15%);
  }
}

.red {
  color: $button-secondary-text-color;
  background-color: #fe4f67;

  &:hover,
  &:focus,
  &:active {
    background-color: lighten(#fe4f67, 5%);
  }
}

.secondary {
  color: $button-secondary-text-color;
  background-color: #3e4049;

  .moving {
    stroke: $button-secondary-text-color;
  }

  &:hover {
    background-color: $button-secondary-bg-color-hover;
  }

  &:focus,
  &:active {
    background-color: $button-secondary-bg-color-pressed;
  }
}

.green {
  height: 50px;

  color: $button-secondary-text-color;
  background-color: $color-green;

  font-weight: 400;


  &:hover {
    background-color: darken($color-green, 10);
  }

  &:focus,
  &:active {
    background-color: darken($color-green, 10);
  }
}
