@import 'Styles/variables.scss';

.link {
    border: none;
    background-color: transparent;

    cursor: pointer;
    outline: none;

    display: flex;

    width: 100%;
    height: 100%;
    padding: 0;

    text-align: left;
    text-decoration: none;

    align-items: center;
    justify-content: flex-start;
}

.linkText {
    text-align: left;
    color: $default-text;
}

.listItem {
    display: flex;

    height: 50px;

    list-style-type: none;

    align-items: center;
    justify-content: flex-start;

    &:hover,
    &:focus,
    &:visited,
    &:active {
      text-decoration: none;

      background-color: rgba(255, 255, 255, .5);
    }


    @media (max-width: 992px) {
      border-bottom: 1px solid rgba(255, 255, 255, .1);
    }
}

.hide {
    @media (min-width: 551px) {
        display: none;
    }
}

.icon {
    display: flex;

    margin-left: 22px;
    margin-right: 16px;

    svg {
      max-width: 18px;
      min-width: 18px;

      max-height: 20px;

      path {
        fill: $default-text;
      }
    }
}
