// sass-lint:disable class-name-format
@import 'Styles/variables.scss';
$react-dates-width-input: 118px;
$react-dates-width-arrow: 24px ;
$react-dates-width-tooltip-arrow: 20px ;
$react-dates-spacing-vertical-picker: 72px ;
$react-dates-color-primary: $blue;
$react-dates-color-primary-shade-1: #fff;
$react-dates-color-primary-shade-2: #fff;
$react-dates-color-primary-shade-3: #fff;
$react-dates-color-primary-shade-4: #fff;
$react-dates-color-secondary: #fff;
$react-dates-color-white: #fff;
$react-dates-color-gray: $default-text;
$react-dates-color-gray-dark: $default-text;
$react-dates-color-gray-light: $default-text;
$react-dates-color-gray-lighter: $default-text;
$react-dates-color-gray-lightest: $default-text;
$react-dates-color-border: rgba(62, 64, 73, 0.3);
$react-dates-color-border-light: rgba(62, 64, 73, 0.3);
$react-dates-color-border-medium: rgba(62, 64, 73, 0.3);
$filter-border-color: rgba(62, 64, 73, 0.3);
$react-dates-color-text: $default-text;
$react-dates-color-text-focus: $default-text;
$react-dates-color-focus: #fff;
$filter-primary: #f5f5f5;
$dropdown-arrow-color: #a4a4a4;
$drodown-border-color: #dbdeeb;
$placeholder-color: #bdc1d2;
$right-margin-btw-blocks: 10px;
$green-btn: #39b210;

@import '~react-dates/css/variables';
@import '~react-dates/css/styles';

.DayPickerNavigation {
  button {
    padding: 9px 12px;

    border: none;
  }

  svg {
    width: 8px;
  }
}

.SingleDatePicker {
  margin: 0 10px;
}

.SingleDatePicker,
.SingleDatePickerInput {
  min-height: 25px;
  border-width: 0;
}

.DateInput {
  position: relative;
  font-weight: 400;

  padding: 5px;
  padding-left: 11px;
  line-height: 1;

  width: 118px;

  display: flex;
  align-items: center;

  border-width: 0;
  border-radius: 9px;
  text-transform: capitalize;

  font-size: 12px;
  height: 32px;

  &::after {
    display: inline-block;

    position: absolute;
    left: inherit;

    top: 13.5px;
    right: 11px;

    width: 8px;
    height: 5.5px;

    content: '';
    transition: transform 150ms;

    border: none;

    background-image: url('../../img/dropdown-arrow.svg');
    background-size: cover;
  }

  &--with-caret {
    &::after {
      transform: rotate(180deg);
    }
  }


  &__input {
    z-index: 1;
    padding: 0;

    cursor: pointer;
  }

  &__display-text {
    padding: 4px 0;

    background: none;

    &::after {
      position: absolute;
      top: 12px;
      right: 11px;

      width: 5px;
      height: 5px;

      content: '';
      transform: rotate(135deg);
    }
  }
}

.SingleDatePickerInput {
  position: relative;

  box-sizing: border-box;

  border-radius: 9px;

  width: 118px;
}

.SingleDatePicker {
  &__picker {
    top: 44px;
    z-index: 2;

    border: 1px solid $filter-border-color;
    border-radius: 3px;

    &--full-screen-portal {
      top: 0;
    }
  }
}

.DayPicker {
  &--horizontal {
    box-shadow: none;
  }

  &__week-header {
    top: 55px;

    color: $default-text;
  }
}

.CalendarDay {
  height: 42px !important;

  border-top: none;
  border-right: none;
  border-bottom: 3px solid #fff;
  border-left: none;

  &__button {
    color: $default-text;
  }

  &--selected-start,
  &--selected-end {
    background-color: transparent;

    button {
      color: $default-text;
      border-radius: 50%;
    }
  }

  &--blocked-out-of-range {
    opacity: .5;

    &:hover {
      cursor: not-allowed;
    }
  }
}

.CalendarMonth {
  table {
    border-collapse: separate;
  }

  &__caption {
    padding-top: 20px;

    text-transform: uppercase;

    font-size: 14px;
  }
}
