@import 'Styles/variables.scss';

$pagination-color: #3e4049;
$pagination-color-selected: #fff;
$pagination-bg-color: rgba(255, 255, 255, 0.3);

$pagination-color-hover: #3e4049;
$pagination-bgc-active: #0094fe;

.pagination {
  display: flex;

  flex-flow: row nowrap;

  @media screen and (max-width: $mobileWidthEndpoint) {
    padding: 0;
  }

  .disabled {
    visibility: hidden;
  }

  .next,
  .previous {
    background-color: inherit;
  }

  li {
    margin-right: 8px;

    list-style-type: none;

    transition: color .3s ease;

    color: $pagination-color;
    background-color: $pagination-bg-color;

    font-size: 12px;

    border-radius: 5px;

    i {
      display: block;

      height: 14px;
    }

    svg {
      width: 8px;

      g {
        transition: fill .3s ease;

        fill: $pagination-color;
      }
    }

    &:hover {
      svg {
        g {
          transition: fill .3s ease;

          fill: $pagination-color-hover;
        }
      }
    }

    &.break-dots {
      background-color: inherit;
      cursor: not-allowed;

      button {
        margin-top: 1.5px;
        align-items: flex-start;
        color: $pagination-color;
        background-color: rgba(255, 255, 255, 0.3);
        border: none;
        border-radius: 5px;
        cursor: pointer;
      }
    }

    &.active {
      background-color: $pagination-bgc-active;
      color: $pagination-color-selected;
      height: 29px;
    }

    a,
    button {
      display: flex;

      width: 29px;
      height: 29px;

      text-decoration: none;

      outline: none;

      align-items: center;
      justify-content: center;
    }

    &:hover {
      cursor: pointer;
    }
  }
}
